.form {
  margin: 1rem 0;
  height: 19rem;
  overflow: auto;
}

.control {
  margin-bottom: 0.5rem;
}

.control label {
  font-weight: bold;
  margin-bottom: 0.25rem;
  display: block;
}

.control input {
  font: inherit;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 20rem;
  max-width: 100%;
}

.actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.actions button {
  font: inherit;
  color: #0e3013;
  cursor: pointer;
  background-color: transparent;
  border: none;
  border-radius: 25px;
  padding: 0.5rem 2rem;
}

.actions button:hover,
.actions button:active {
  background-color: #0e3013;
  color: white;
}

.actions .submit {
  border: 1px solid #0e3013;
  background-color: #0e3013;
  color: white;
}

.actions .button--alt {
  color: #0e3013;
}
.actions .button--alt:hover,
.actions .button--alt:active {
  background-color: #5a1a01;
  border-color: #5a1a01;
  color: white;
}

.actions .submit:hover,
.actions .submit:active {
  background-color: #72a542;
}

.invalid p {
  color: #ca3e51;
}

.invalid input {
  border-color: #aa0b20;
  background-color: #ffeff1;
}

button:disabled,
button:disabled:hover,
button:disabled:active {
  cursor: not-allowed;
  background-color: #ccc;
  color: #3f3f3f;
  border-color: #ccc;
}
